.banner{
    background-image:url("../../images/Designers.png");
    background-size:100vmax 50vmax;
    background-repeat: no-repeat;
    height:100vmin;
    width:100vw;
    display: flex;
    flex-direction: column;
    text-align: center;
    align-items: center;
    justify-content: center;
    color: white;
}
.banner >h1{
    margin: 3vmax;
    font:400 2vmax "Roboto";
}
.banner >p{
    font: 300 1.5vmax "Roboto";
}
.banner >a >button{
margin-bottom:1px;
cursor: pointer; 
background-color: white;
border: 1px solid white;
border-radius: 10px;
padding: 1vmax;
transition:all 0.5vmax;
width: 9vmax;
font: 500 1vmax "Roboto";
}
.banner >a >button:hover{
    background-color: rgba(255,255,255,0);
    color: white;
}
.banner::after{
    content:"";
    width: 100vw;
    height: 100vmin;
    background-color: white;
    position:absolute;
    top:0%;
    left: 0;
    clip-path: polygon(100% 100%,0 100%,100% 100%);
    max-width: 100%;
}
.homeHeading{
    text-align: center;
    font-family: Roboto;
    font-size: 1.4vmax;
    border-bottom: 1px solid rgba(21,21,21,0.5);
    width: 20vmax;
    padding: 1vmax;
    margin: 5vmax auto;
    color: rgba(39, 38, 38, 0.87);
}
.productCard{
    width:17vmax;
    display: flex;
    flex-direction:column;
    text-decoration: none;
    justify-content: center;
    color: rgb(48,48,48);
    margin: 2vmax;
    transition: all 0.5s;
    padding: 0.5vmax 1vmax 0.5vmax 3vmax;
    border-radius:10px;

}

.productCard >img{
    width:14vmax;
    justify-content: center;
}

.productCard >div{
    margin: 0.5vmax;
    display: flex;
    justify-content: flex-start;
    align-items: center;
}
.productCardSpan{
    margin: 0.5vmax;
    font: 300 0.7vmax "Roboto";
}
.productCard >p{
    font-family: "Roboto";
    font-size: 1.2vmax;
    margin: 1vmax 0.5vmax;
    margin-bottom: 0;
}
.productCard >span{
    margin: 0.5vmax;
    color: tomato;
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    font-size: 1vmax;
}
.productCard:hover{
    box-shadow: 0 0 5px rgba(15, 15,15, 0.26);

    transform: translateY(-1vmax);
}
.container{
    display: flex;
    margin: 2vmax auto;
    width: 100vw;
    flex-wrap: wrap;
    justify-content: center;
    
    
}

.ProductContainer{
    display: flex;
    justify-content: space-between;
}
.backendProduct{
    width:80%;
    display: flex;
    flex-direction:column;
    text-decoration: none;
    color: rgb(48,48,48);
    margin: 4vmax;
    transition: all 0.5s;
    padding-bottom: 0.5vmax;

   
}

.backendProduct:hover{
    box-shadow: 0 0 5px rgba(15, 15,15, 0.26);
    transform: translateY(-1vmax);
}
.backendProduct >p{
    margin: 0.5vmax 0.5vmax;
    margin-bottom: 0;
    font: 300 1.2vmax "Roboto"
}
.backendProduct > ReactStars{
    color:tomato;
    size:5vmax
}
.backendProduct >div >img{
    width: 14vmax;
}

.ProductContainer{
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 20px; 
  
}

@media screen and (max-width:600px){
    .banner{
         background-size:cover; 
        background-repeat: no-repeat;
        width: 150%;
        height: 80vmax;

    }
    .banner::after{
        content:"";
        width: 100vw;
        height: 100vmin;
        background-color: white;
        position:absolute;
        top:0%;
        left: 0;
        clip-path: polygon(100% 100%,0 100%,100% 100%);
        max-width: 100%;
    }
    
    .productCard >p{
        font-size: 1.7vmax;
    }
    .productCard >div{
        margin: 0vmax;
        display: block;
    }
    .productCard >span{
        font-size: 1.5vmax;
    }
    .productCard >div >span{
        margin: 0  0.5vmax;
        font:300 1vmax "Roboto";
    } 
}
