.updateProfileContainer{
    width: 100vw;
    height: 100vh;
    display: flex;
    max-width: 100%;
    justify-content: center;
    align-items: center;
    background-color: rgb(227, 231, 235);
    position: fixed;
    top: 0%;
    left: 0%;
}
.updateProfileBox{
    background-color: rgb(212, 207, 212);
    width: 25vw;
    height: 70vh;
    box-sizing: border-box;
    overflow:hidden;
}
.updateProfileHeading{
    text-align: center;
    color: rgba(0, 0, 0, 0.667);
    font: 400 1.3vmax "Roboto";
    padding: 1vmax;
    border-bottom: 1px solid rgba(0, 0, 0, 0.205);
    width: 50%;
    margin: auto;
}
 .updateProfileform{
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: auto;
    padding: 2vmax;
    height: 70%;
    justify-content: space-evenly;
    transition: all 0.5s;

}


.updateProfileform >div{
display: flex;
width: 100%;
align-items: center;
}

.updateProfileform >div >input{
padding: 1vmax 3vmax;
padding-right: 1vmax;
width: 100%;
box-sizing: border-box;
border: 1px solid rgba(0,0,0, 0.267);
border-radius: 4px;
font: 300 0.9vmax cursive;
outline: none;
}

.updateProfileform >div >svg{
position: absolute;
transform: translateX(1vmax);
font-size: 1.6vmax;
color: rgba(0,0,0, 0.678);
}

#updateProfileImage >img{
    width: 3vmax;
    border-radius: 100%;
    margin: 1vmax;

}
#updateProfileImage >input{
    display: flex;
    padding: 0%;
    
}
#updateProfileImage >input::file-selector-button{
    cursor: pointer;
    width: 100%;
    z-index: 2;
    height: 5vh;
    border: none;
    margin: 0%;
    font: 400 0.8vmax cursive;
    transition: all 0.5s;
    padding: 0 1vmax;
    color: rgba(0, 0, 0, 0.637);
    background-color: rgb(252, 249, 249);
}

.updateProfileBtn{
    border: none;
    background-color: tomato;
    color: white;
    font: 300 0.9vmax "Roboto";
    width: 100%;
    padding: 0.8vmax;
    cursor: pointer;
    transition: all 0.5s;
    border-radius: 4px;
    outline: none;
    box-shadow: 0 2px 5px rgba(0,0,0, 0.219);
}
.updateProfileBtn:hover{
    background-color: rgb(194, 58, 34);
}

@media screen and (max-width:600px){
    .updateProfileBox{
        width:100vw;
        height: 95vh;
    }
    
   .updateProfileform{
    padding: 5vmax;
   } 
   .updateProfileform >div >input{
    padding: 2.5vmax 5vmax;
    font: 300 1.7vmax cursive;
   }
   .updateProfileform >div >svg{
    font-size: 3vmax;
   }
   
   #updateProfileImage >input::file-selector-button{
    height: 7vh;
    font: 400 1.5vmax cursive;
   }
   .updateProfileBtn{
    font:300 2vmax "Roboto";
    padding: 1.8vmax;
   }
}